@import "normalize.css";
@import "base/_fonts.scss";
@import "layout/_footer.scss";
@import "layout/_header.scss";

@mixin flex($parameter1,$parameter2,$parameter3,$parameter4) {
  display: $parameter1;
  flex-direction: $parameter2;
  justify-content: $parameter3;
  align-items: $parameter4;
}

@mixin wider-margin {
  margin: 0 100px;
}

@mixin align-right {
  justify-content: flex-end;
}

@mixin align-left {
  justify-content: flex-start;
}

@mixin behavior {
  transition: all 300ms ease-in-out;
}

@mixin column-margin {
  margin: 0 0 0 80px;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #ffff;
  font-family: 'Roboto', sans-serif;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.container {
  &__align {
    @include flex(flex, row, space-between, center);
    height: 48.6px;
    @include wider-margin;
  }

  &--left {
    width: 50%;
    padding: 0px 15px;
    @include flex(flex, row, space-between, center);
    justify-content: flex-start;
  }

  &--right {
    width: 50%;
    @include flex(flex, row, space-between, center);
    justify-content: flex-end;
    padding: 0px 15px;
  }
}

.banner {
  display: block;
  width: 100%;
  position: relative;

  &__fluid {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
  }

  &__order {
    position: relative;
    width: 100%;
    padding-right: -15px;
    padding-left: -15px;
    margin-right: auto;
    margin-left: auto;
    flex-wrap: wrap;
    @include flex(flex, row, space-between, center);

    &--column {
      position: relative;
      width: 50%;
      height: 100%;
      padding: 0 15px;
      @include flex(flex, row, space-between, center);
    }
  }

  &__substance {
    position: relative;
    width: 50%;
   @include flex(flex, column, space-between, center);
    @include align-right;
  }

  &__image {
    position: relative;
    //width: 100%;
    height: auto;
    @include align-left;
  }
}

.first-banner {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  background-image: url("https://demo.themeies.com/silon/images/bg-1.jpg");
}

.second-banner {
  background-color: #e9e2d8;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.content__button:hover.content__button--content {
  color: #181886;
  background-color: white;
}

.content {
  position: relative;
  text-align: center;
  padding: 15px 15px;
  @include flex(flex, column, space-between, center);
  flex-wrap: nowrap;

  &__button {
    background: #ff5353;
    width: 170px;
    height: 51.2px;
    border-radius: 8px;

    &--content {
      display: inline-block;
      text-transform: uppercase;
      color: #ffff;
      padding: 20px 25px;
      text-align: center;
      text-decoration: none;
      margin-left: auto;
      margin-right: auto;
      font-size: 14px;
      font-weight: 500;
    }
  }
}

.first-banner__subtitle {
  height: 11.2px;
  text-transform: uppercase;
  color: #565656;
  margin: 0;
}


h1 {
  font-size: 50px;
  text-align: center;
}

.featured-product__container {
    @include flex(flex, column, space-between, center);
    display: block;
    flex-wrap: wrap;
    @include wider-margin;
  }

h2 {
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
  padding: 0px 0 50px 0;
}

.ti-shopping-cart {
  text-decoration: none;
  position: relative;
  font-size: 18px;
  padding: 10px;
  box-sizing: border-box;
}

.ti-heart {
  text-decoration: none;
  position: relative;
  font-size: 18px;
  padding: 10px;
}

.ti-search {
  text-decoration: none;
  position: relative;
  font-size: 18px;
  padding: 10px;
}

.ti-star {
  text-decoration: none;
  position: relative;
  text-align: center;
  color: #ff5353;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

.columns {
  @include flex(flex, row, space-around, center);
  position: relative;
  margin-bottom: 50px;
  display: flex;
}

.product:hover {
  .product__item--hover {
    visibility: visible;
    position: relative;
    top: -200px;
    transition: top 300ms ease-in-out;
  }
}

.product {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;

  &__item {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    box-sizing: border-box;

    &--hover {
      top: -180px;
      position: relative;
      visibility: hidden;
      display: flex;
      text-decoration: none;
      justify-content: center;
    }

    &--details {
      :hover.item__name {
        color: #ff5353;
      }

      p.item__name {
        font-size: 14px;
        color: #888;
        margin-top: 25px;
      }
    }
  }
}

.item {
  &__rating {
    text-align: center;
  }

  &__price {
    position: relative;
    margin: 10px 0 0 0;
    padding: 0;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    color: black;
  }
}

.entrance {
  position: relative;
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 40px;
  text-decoration: none;
  margin: 5px;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.entrance:hover {
  background-color: #ff5353;
  color: white;
}

.ti-eye {

  font-size: 15px;
  text-decoration: none;
}

.second-banner__title {
  font-family: "Dancing Script", cursive;
  font-weight: 700;
  margin: 0 0 33px;
  font-size: 58px;
}

.second-banner__title--subtitle {
  font-weight: 300;
  text-transform: uppercase;
  margin: 0 0 54px;
  text-align: center;
  font-size: 24px;
}


@media (min-width: 20px) and (max-width: 423px) {
  .navigation-bar {
    height: 90.4px;
    margin: 0 20px;
    @include flex(flex, row, space-between, center);

    &__item {
      @include flex(flex, row, space-between, center);

      &--element {
        position: relative;
        display: flex;
        align-items: flex-start;
        justify-content: center;

        :hover.icons-header {
          color: #ff5353;
        }
      }
    }
  }
  .header__top {
    display: none;
  }
  .menu {
    list-style: none;
    display: none;

    &__item {
      list-style: none;
      display: none;
      @include behavior;
    }

    :hover.menu__item {
      color: #ff5353;

    }
  }
  .hamburger-menu {
    visibility: visible;
  }
  .banner__order--column {
    width: 100%;
  }
  .banner__order {
    flex-wrap: wrap-reverse;
  }
  .banner__substance {
    padding: 30px;
  }
  .second-banner {
    background-color: #e9e2d8;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .featured-product__container {
    @include flex(flex, column, space-between, center);
    display: block;
    flex-wrap: wrap;
    margin: 0 0 0 0;
  }
  .product__item--hover {
    flex-wrap: wrap;
  }
  .columns {
    @include flex(flex, column, space-around, center);
  }
}

